export default function Timeline() {
    const containerRef = useRef(null);
    const { scrollYProgress } = useScroll({
        target: containerRef,
        offset: ["start center", "end center"],
    });

    const scaleY = useSpring(scrollYProgress, {
        stiffness: 100,
        damping: 30,
        restDelta: 0.001
    });

    return (
        <section ref={containerRef} className="relative bg-blush-white py-24 px-4 overflow-hidden">
            <div className="max-w-6xl mx-auto">
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    className="text-center mb-20"
                >
                    <h2 className="font-script text-6xl text-dusty-rose mb-4">Our Love Story</h2>
                    <div className="h-1 w-24 bg-gold mx-auto" />
                </motion.div>

                <div className="relative">
                    {/* Vertical Line */}
                    <motion.div
                        style={{ scaleY, transformOrigin: "top" }}
                        className="absolute left-1/2 top-0 h-full w-0.5 bg-gold/30 -translate-x-1/2 hidden md:block"
                    />

                    <div className="space-y-24">
                        {milestones.map((item, index) => (
                            <TimelineItem key={index} item={item} index={index} />
                        ))}
                    </div>
                </div>
            </div>
        </section>
    );
}

function TimelineItem({ item, index }: { item: any; index: number }) {
    const isEven = index % 2 === 0;

    return (
        <div className={`relative flex flex-col md:flex-row items-center ${isEven ? "md:flex-row-reverse" : ""}`}>
            {/* Circle Marker */}
            <div className="absolute left-1/2 top-0 -translate-x-1/2 z-20 hidden md:block">
                <motion.div
                    initial={{ scale: 0 }}
                    whileInView={{ scale: 1 }}
                    viewport={{ once: true }}
                    className="w-10 h-10 rounded-full bg-white border-2 border-gold flex items-center justify-center text-gold shadow-lg"
                >
                    {item.icon}
                </motion.div>
            </div>

            {/* Content Card */}
            <motion.div
                initial={{ opacity: 0, x: isEven ? 50 : -50 }}
                whileInView={{ opacity: 1, x: 0 }}
                viewport={{ once: true, margin: "-100px" }}
                transition={{ duration: 0.8, ease: "easeOut" }}
                className={`w-full md:w-5/12 ${isEven ? "md:pl-12" : "md:pr-12"} mb-8 md:mb-0`}
            >
                <div className="bg-white/80 backdrop-blur-sm p-8 rounded-3xl shadow-xl border border-pearl-pink hover:shadow-2xl transition-shadow duration-500">
                    <span className="font-display text-gold text-2xl mb-2 block">{item.year}</span>
                    <h3 className="font-display text-3xl text-charcoal mb-4">{item.title}</h3>
                    <p className="font-body text-gray leading-relaxed mb-6">{item.description}</p>
                    <div className="relative h-48 w-full overflow-hidden rounded-2xl group">
                        <Image
                            src={item.image}
                            alt={item.title}
                            fill
                            className="object-cover transition-transform duration-700 group-hover:scale-110"
                        />
                    </div>
                </div>
            </motion.div>

            {/* Spacer for MD screens */}
            <div className="hidden md:block w-2/12" />
        </div>
    );
}
